<template>
  <div class="count-point">
    <div class="content clearfix">
      <div class="content-l fl">
        <!-- 添加玩家游戏名次 -->
        <el-form ref="myForm" :model="formData" :rules="rules">
          <el-form-item class="clearfix" prop="rank1">
            <img src="../assets/player1.jpeg" alt="" class="content-l-img fl">
            <el-select placeholder="请选择玩家名次" v-model="formData.rank1" class="fl">
              <el-option v-for="option in rankOptions" :key="option.value" :label="option.label" :value="option.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="clearfix" prop="rank2">
            <img src="../assets/player2.jpeg" alt="" class="content-l-img fl">
            <el-select placeholder="请选择玩家名次" v-model="formData.rank2" class="fl">
              <el-option v-for="option in rankOptions" :key="option.value" :label="option.label" :value="option.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="content-r clearfix">
        <el-button type="primary" class="fl" @click="addToTable" >添加记录</el-button>
        <p class="content-r-rule fr" @click="dialogVisible = true">显示规则</p>
      </div>
    </div>
    <!-- 玩家名次表格 -->
    <el-table
    :data="tableData"
     style="width:100%"
     >
      <el-table-column align="center" :label="curDate">
        <el-table-column align="center" label="gina">
          <el-table-column
            prop="rank1"
            label="名次"
            align="center"
          >
            <template slot-scope="scope">
              <img src="../assets/player1.jpeg" alt="" class="tr-img fl">
              <p class="tr-word fl">{{scope.row.rank1}}</p>
            </template>
          </el-table-column>
          <el-table-column
            prop="free1"
            label="金额"
            align="center"
          >
            <template slot-scope="scope">
              <span v-if="scope.row.free1 > 0">+</span>{{scope.row.free1}}
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column align="center" label="wj">
          <el-table-column
            prop="rank2"
            label="名次"
            align="center"
          >
            <template slot-scope="scope">
              <img src="../assets/player2.jpeg" alt="" class="tr-img fl">
              <p class="tr-word fl">{{scope.row.rank2}}</p>
            </template>
          </el-table-column>
          <el-table-column
            prop="free2"
            label="金额"
            align="center"
          >
          </el-table-column>
        </el-table-column>
      </el-table-column>
    </el-table>
    <div class="result clearfix">
      <p class="fl">合计：</p>
      <p class="fl clearfix"><img class="result-img fl" src="../assets/player1.jpeg" alt=""><span class="result-word ml10 fl">{{playARes}}</span></p>
      <p class="fl clearfix ml20"><img class="result-img fl" src="../assets/player2.jpeg" alt=""><span class="result-word ml10 fl">{{playBRes}}</span></p>
    </div>
    <div class="clearfix" v-if="tableData.length > 0">
      <div class="fl">综上所述： </div>
      <div class="explain fl" v-if="playARes > playBRes">wj 需支付 gina {{playARes - playBRes}} 元</div>
      <div class="explain fl" v-if="playARes === playBRes">双方一个字儿都不用付~</div>
      <div class="explain fl" v-if="playARes < playBRes">gina 需支付 wj {{playBRes - playARes}} 元</div>
    </div>
    <p class="mt10">*ps: 谁菜已经很明显了吧?!</p>
    <el-dialog
      title="pp龙虎榜规则"
      :visible.sync="dialogVisible"
      width="80%"
      center>
      <h4 class="mt0">gina规则如下：</h4>
      <p>1.第1名 +10</p>
      <p>2.第2、3、4名 +3</p>
      <p>3.第8名 -3</p>
      <el-divider></el-divider>
      <h4>wj规则如下：</h4>
      <p>1.第1名 +3</p>
      <p>2.第5、6、7名 -3</p>
      <p>3.第8名 -10</p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { formatTime } from "../utils";
var $data = {
  curDate: null,
  dialogVisible: false,
  rules: {
    rank1: [{ required: true, message: "请选择玩家名次", trigger: "blur" }],
    rank2: [{ required: true, message: "请选择玩家名次", trigger: "blur" }]
  },
  formData: {
    rank1: null,
    rank2: null
  },
  // 排名选项
  rankOptions: [
    {
      label: "第1名",
      value: 1
    },
    {
      label: "第2名",
      value: 2
    },
    {
      label: "第3名",
      value: 3
    },
    {
      label: "第4名",
      value: 4
    },
    {
      label: "第5名",
      value: 5
    },
    {
      label: "第6名",
      value: 6
    },
    {
      label: "第7名",
      value: 7
    },
    {
      label: "第8名",
      value: 8
    }
  ],
  // 比赛记录数据
  tableData: []
};
export default {
  data() {
    return $data;
  },
  computed: {
    playARes() {
      let len = $data.tableData.length;
      if (len) {
        let sum = 0;
        $data.tableData.forEach(item => {
          sum += item.free1;
        });
        return sum;
      }
      return 0;
    },
    playBRes() {
      let len = $data.tableData.length;
      if (len) {
        let sum = 0;
        $data.tableData.forEach(item => {
          sum += item.free2;
        });
        return sum;
      }
      return 0;
    }
  },
  mounted() {
    this.curDate = formatTime(new Date(), "yyyy-MM-dd");
  },
  methods: {
    /**
     * 添加比赛记录
     */
    addToTable() {
      this.$refs.myForm.validate(valid => {
        if (valid) {
          // 同一名次限制
          if ($data.formData.rank1 === $data.formData.rank2) {
            this.$message({
              message: "俩人怎么可能是同一名次，4不4傻",
              type: "warning"
            });
          } else {
            let { rank1, rank2 } = $data.formData;
            let free1 = 0;
            let free2 = 0;
            // gina规则
            if (rank1 === 1) {
              free1 = 10;
            } else if ([2, 3, 4].indexOf(rank1) !== -1) {
              free1 = 3;
            } else if (rank1 === 8) {
              free1 = -3;
            }
            // wj 规则
            if (rank2 === 1) {
              free2 = 3;
            } else if ([5, 6, 7].indexOf(rank2) !== -1) {
              free2 = -3;
            } else if (rank2 === 8) {
              free2 = -10;
            }

            let obj = {
              rank1: "第" + rank1 + "名",
              free1: free1,
              rank2: "第" + rank2 + "名",
              free2: free2
            };
            $data.tableData = [...$data.tableData, obj];
            this.$message({
              message: "添加记录成功",
              type: "success"
            });
            // 添加成功初始化输入框
            $data.formData = {
              rank1: null,
              rank2: null
            };
          }
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.count-point {
  padding: 0 10px;
  .content {
    margin: 16px 10px 20px 10px;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px #e4d7d7;
    border-radius: 5px;
    &-l {
      &-img {
        width: 40px;
        height: 40px;
        border-radius: 30px;
        margin-right: 15px;
      }
    }
    &-r {
      &-rule {
        font-size: 14px;
        color: #666;
      }
    }
  }
  .tr-img {
    width: 20px;
    height: 20px;
    border-radius: 20px;
  }
  .tr-word {
    margin: 0;
    margin-left: 5px;
  }
  .result {
    &-img {
      width: 30px;
      height: 30px;
      border-radius: 20px;
      margin-top: -5px;
    }
    &-word {
      color: red;
    }
  }
  .explain {
    color: blue;
  }
}
</style>



